import { Component,ViewChild, ElementRef } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, } from 'ionic-angular';
import { DesafioService } from './../desafios/desafios.service';
import { AuthService } from './../../providers/auth-service/auth-service'
import { HttpClient } from '@angular/common/http';
import 'rxjs/add/observable/throw';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import { Geolocation } from '@ionic-native/geolocation';

declare const google;
@IonicPage()
@Component({
  selector: 'page-desafio-modal',
  templateUrl: 'desafio-modal.html',
})
export class DesafioModalPage {
  desafioid: number = this.navParams.get('id');
  titulo: string = this.navParams.get('titulo');
  descricao: string = this.navParams.get('descricao');
	categoria: String = this.navParams.get('categoria');
	pontuacao: Number = this.navParams.get('pontuacao');
	duracao: Number = this.navParams.get('duracao');
	status: String = this.navParams.get('status');
  arquivoA?: string = this.navParams.get('arquivoA');
  arquivoB?: String = this.navParams.get('arquivoB');
	longitude?: String = this.navParams.get('longitude');
  latitude?: String = this.navParams.get('latitude');
  
  @ViewChild('map') mapElement: ElementRef;
  map: any;
  directionsService = new google.maps.DirectionsService;
  directionsDisplay = new google.maps.DirectionsRenderer;


  constructor(public http: HttpClient,
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    public desafioService: DesafioService,
    public authService: AuthService,
    private geolocation: Geolocation,
  ) {}
  
  location = false;

  tempoDesafio = 0;
  
  ionViewDidLoad() {
    if(this.latitude){
      this.location = true;
      this.getLocation();
    }
    if (this.status == 'notyet'){
      this.desafioStatus ='Disponível';
    }else{
      if (this.status == 'pending') {
        this.desafioStatus ='Pendente';
      }else{
        this.desafioStatus ='Concluído';
      }
    }
  }
  
  
  getLocation(){
    this.geolocation.getCurrentPosition().then((resp) => {
      let lat = resp.coords.latitude;
      let lon = resp.coords.longitude;
      this.initMap(lat,lon);
    }).catch((error) => {
      console.log('Erro ao tentar encontrar a localização', error);
    });
  }
  
    initMap(a,b) {
      var myLatLng = {lat: a, lng: b};
      var pontoColeta1 = new google.maps.LatLng(-4.979256, -39.056513);
      var pontoSaida = new google.maps.LatLng(a, b);

      this.map = new google.maps.Map(this.mapElement.nativeElement, {
        zoom: 18,
        center: myLatLng
      });
      this.directionsDisplay.setMap(this.map);

      var marker = new google.maps.Marker({
        position: myLatLng,
        map: this.map,
        title: "Aqui está você!"
      });
      
      this.calculateAndDisplayRoute(myLatLng, pontoColeta1)
    }
  
  calculateAndDisplayRoute(a,b) {
    this.directionsService.route({
      origin: a,
      destination: b,
      travelMode: 'WALKING'
    }, (response, status) => {
      if (status === 'OK') {
        this.directionsDisplay.setDirections(response);
        
      } else {
        window.alert('Directions request failed due to ' + status);
      }
    });
  }

  concluirDesafio(){
    let desafioid = this.desafioid;
    let desafio = this.desafioService.desafios.find(function (obj) { return obj.desafioid === desafioid }); 
    desafio.status = 'done';
    let user = this.authService.currentUser;
    for (let i = 0; i < user.desafios.length; i++) {
      if (user.desafios[i].desafioId==desafioid){
        user.desafios[i].status='done';
      }
    }

    this.authService.updateDesafio(user).subscribe(response => {
      this.closeModal();
    },
      error => {
        console.log(error);
      });
  }

  cancelaDesafio(){
    let id = this.desafioid;
    let desafio = this.desafioService.desafios.find(function (obj) { return obj.desafioid === id }); 
    desafio.status = 'notyet';
    let user = this.authService.currentUser;
    for (let i = 0; i < user.desafios.length; i++) {
      if (user.desafios[i].desafioId==id){
        user.desafios.splice(i, 1);
      }
    }

    this.authService.updateDesafio(user).subscribe(response => {
      this.closeModal();
    },
      error => {
        console.log(error);
      });
  }

  closeModal() {
    this.viewCtrl.dismiss();
  }

  desafioStatus = '';

  aceitarDesafio(){
    let desafioid = this.desafioid;
    let desafio = this.desafioService.desafios.find(function (desafio) { return desafio.desafioid === desafioid });
    desafio.status="pending";
    let user = this.authService.currentUser;
    if(user.desafios){
      user.desafios.push({desafioId:desafio.desafioid,status:desafio.status});
    }else{
      user["desafiosId"] = [{desafioId:desafio.desafioid,status:desafio.status}];
    };
    this.authService.updateDesafio(user).subscribe(response => {
      // let cancela = this.cancelaDesafio();
      // setTimeout('cancela()',10000);
      this.closeModal();
    },
      error => {
        console.log(error);
      });
  };
  
  contagem_tempo(){	             
    let segundos = 0;
      
    segundos = segundos - 1;
    if (segundos == -1) {
     segundos = 0;
    }                 
  }
}



