import { Injectable } from '@angular/core';
import { Usuario } from './../../pages/cadastro/usuario.model';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/observable/throw';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import { HttpClient } from '@angular/common/http';
import { Http, Response } from '@angular/http';

@Injectable()
export class AuthService {
  constructor(private http: Http) {}

  currentUser: Usuario;
  token = '';

  public login(credentials) {
    if (credentials.email === null || credentials.password === null) {
      return Observable.throw('Nenhum campo pode ficar vazio');
    } else {
      let url = '/api/v1/login';
      return this.http
        .post(url, credentials)
        .map((response: Response) => {
          this.token = response.json().token;
          this.currentUser = response.json().data;
          localStorage.setItem('token', this.token);
          // localStorage.setItem('currentUser', this.currentUser.toString());
          return this.currentUser;
        })
        .catch((error: Response) => Observable.throw(error));
    }
  }

  public cadastraUsuario(usuario) {
    let url = 'api/v1/usarios';
    return this.http
      .post(url, usuario)
      .map((response: Response) => {
        this.currentUser = response.json();
        console.log(this.currentUser);
      })
      .catch((error: Response) => Observable.throw(error));
  }
  public registrarHabito(habitos) {
    let url = 'api/v1/hashabito';
    return this.http.post(url, habitos).map((response) => {
      console.log(response);
    }).catch((error) => Observable.throw(error))
  }
  public getUserInfo(): Usuario {
    return this.currentUser;
  }
  public getHabitos() {
    let url = 'api/v1/habitos';
    return this.http.get(url).map((response) => {
      response.json()
    })
  }
  public updateDesafio(user) {
    let url = '/api/v1/usuarios/' + this.currentUser.usuarioid;
    return this.http
      .put(url, { user: user, token: this.token })
      .map((response: Response) => {
        console.log(response);
        return 'deu bom';
      })
      .catch((error: Response) => Observable.throw(error));
  }

  public logout() {
    this.currentUser = null;
    this.token = '';
    localStorage.clear();
    return Observable.create(observer => {
      observer.next(true);
      observer.complete();
    });
  }

  public salvaMuda(muda) {
    let url = '/api/v1/muda/';
    return this.http
      .post(url, muda)
      .map((response: Response) => {
        return response;
      })
      .catch((error: Response) => Observable.throw(error));
  }

  public getMuda() {
    let url = '/api/v1/muda/' + this.currentUser.usuarioid;
    return this.http
      .get(url)
      .map((response: Response) => {
        return response.json();
      })
      .catch((error: Response) => Observable.throw(error));
  }

  public getMudas() {
    let url = '/api/v1/muda';
    return this.http
      .get(url)
      .map((response: Response) => {
        return response.json();
      })
      .catch((error: Response) => Observable.throw(error));
  }

  public updateMuda(muda, pontos) {
    console.log(muda.mudaid);
    let url = `/api/v1/muda/${muda.mudaid}`;
    return this.http
      .put(url, { pontos: pontos })
      .map((response: Response) => {
        console.log(response);
        return 'deu bom';
      })
      .catch((error: Response) => Observable.throw(error));
  }
}



